import store from './store';
import Vue from 'vue'

Vue.mixin({
	methods: {
		t,
	}
})

const initialStateElement = document.getElementById('twofactor-totp-initial-state')
if (initialStateElement) {
	const state = parseInt(initialStateElement.value, 10)
	store.replaceState({
		totpState: state
	})
}

import PersonalTotpSettings from './components/PersonalTotpSettings'

const View = Vue.extend(PersonalTotpSettings)
new View({
	store,
}).$mount('#twofactor-totp-settings')
